.vrect{
	 width: 98px;
	 height: 98px;
	 margin: 0px;
	 padding: 0px;
	 border: 1px solid grey;
	 background-color: red;
	 left:300px;
	 top: 200px;
	 position: absolute;
	 -webkit-transition: all .1s ease;
	 -o-transition: all .1s ease;
	 transition: all .1s ease;
}
.born {
	 -webkit-transform: scale(0);
	     -ms-transform: scale(0);
	         transform: scale(0);
	 -webkit-animation: move .05s .1s ease forwards;
	         animation: move .05s .1s ease forwards;
}
@-webkit-keyframes move{
	0% {-webkit-transform: scale(0.5);transform: scale(0.5);opacity: 0.5}
	80% {-webkit-transform: scale(1.1);transform: scale(1.1);opacity: 0.5;}
	100% {-webkit-transform: scale(1);transform: scale(1);opacity: 1;}
}
@keyframes move{
	0% {-webkit-transform: scale(0.5);transform: scale(0.5);opacity: 0.5}
	80% {-webkit-transform: scale(1.1);transform: scale(1.1);opacity: 0.5;}
	100% {-webkit-transform: scale(1);transform: scale(1);opacity: 1;}
}

.vrect span {
	display: none;
}
.vrect p{
	margin: 0px;
	padding: 0px;
	line-height: 98px;
	height: 100%;
	width: 100%;
	font-size: 2.4em;
	text-align: center;
}


